.table-container {
  border-collapse: collapse;
  width: 100%;
}

th,
td {
  text-align: left;
  padding: 8px;
}

tr:nth-child(even) {
  background-color: #f2f2f2;
}

th {
  background-color: darkgrey;
  color: white;
}

.subTitle {
  font-size: x-small;
}
.title {
  font-size: medium;
}

.header-Cell {
  display: grid;
  grid-row-gap: 2px;
}
